.footer {
	background-color: #717cff;

	// .footer__content
	&__content {
		padding: 65px 0 45px;
		@media (max-width: $md1+px) {
			padding: 25px 0 10px;
		}
	}

	// .footer__row
	&__row {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		@media (max-width: $md1+px) {
			display: block;
			text-align: center;
		}
	}

	// .footer__info
	&__info {}

	// .footer__logo
	&__logo {}

	// .footer__text
	&__text {
		max-width: 343px;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.78;
		color: #eeeeee;
		margin: 25px 0 0;
		@media (max-width: $md1+px) {
			margin: 25px auto 0;
		}
	}

	// .footer__links
	&__links {
		display: flex;
		@media (max-width: $md1+px) {
			margin: 25px 0 0;
			justify-content: center;
		}
	}

	// .footer__icons
	&__icons {
		display: flex;
		align-items: flex-end;
		:nth-child(2) {
			margin: 0 28px;
		}
		@media (max-width: $md1+px) {
			justify-content: center;
			margin: 25px 0 0;
		}
	}

	// .footer__description
	&__description {
		font-size: 18px;
		font-weight: 500;
		line-height: 1.78;
		color: #eeeeee;
		@media (max-width: $md1+px) {
			text-align: center;
			margin: 25px 0 0;
		}
	}
}


.links-footer {

	// .links-footer__left
	&__left {
		margin: 0 20px 0 0;
		@media (min-width: $md1+px) {
				margin: 0 105px 0 0;
		}
	}

	// .links-footer__right
	&__right {
	}

	// .links-footer__link
	&__link {
		display: block;
		font-size: 18px;
		font-weight: 500;
		line-height: 2.72;
		color: #eeeeee;
	}
}