.header {
  overflow: hidden;
  // .header__content
  background: url('../../img/header/person.png') 58vw 100% no-repeat, url('../../img/header/oval.png') 58vw 175% no-repeat;
  @media (max-width: $md1 + px) {
    background: none;
  }
  &__content {
    padding: 40px 0 50px;
  }

  // .header__cap
  &__cap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: $md1 + px) {
      justify-content: center;
      &::before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 134px;
        background-color: #fff;
        z-index: 3;
      }
    }
    @media (max-width: $md3 + px) {
      &::before{
        width: 100%;
        height: 52px;
      }
    }
  }

  // .header__logo
  &__logo {
    z-index: 4;
    @media (max-width: $md1 + px) {
      margin: 0 auto;
    }
  }

  // .header__right
  &__right {
    display: flex;
    align-items: center;
  }

  // .header__nav
  &__nav {
    margin: 0 65px 0 0;
    @media (max-width: $md1 + px) {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: -100%;
      text-align: center;
      background-color: #2f2f2f;
      opacity: 0;
      margin: 0;
      transition: 0.3s;
      z-index: 2;
      &._active{
        opacity: 1;
        left: 0;
      }
    }
  }

  // .header__link
  &__link {
    font-size: 17px;
    font-weight: 500;
    color: #373737;
    margin: 0 65px 0 0;

    @media (max-width: $md1+px) {
      display: block;
      font-size: 30px;
      padding: 0 30px;
      margin: 90px 0 0;
      color: #fff;
      &:first-child {
        margin: 225px 0 0;
      }
    }

    @media (max-width: $md3 + px) {
      margin: 60px 0 0;
      color: #fff;
      &:first-child {
        margin: 100px 0 0;
      }
    }

    &:last-child {
      margin: 0;
    }
  }

  // .header__button
  &__button {
    cursor: pointer;
    font-size: 17px;
    font-weight: 500;
    color: #ffffff;
    width: 111px;
    height: 42px;
    padding: 12px 30.5px 12px;
    border-radius: 11px;
    background-color: #717cff;
    z-index: 3;
    @media (max-width: $md4 + px) {
      width: 70px;
      height: 32px;
      padding: 8px 10px 8px;
    }
  }

  // .header__info
  &__info {
    margin: 140px 0 0;
    @media (max-width: $md1 + px) {
      margin: 80px 0 0;
    }
  }
}

.info-header {

  // .info-header__tilte
  &__tilte {
    max-width: 632px;
    font-size: 56px;
    font-weight: 900;
    color: #2f2f2f;

    span {
      color: #717cff;
    }
    @media (max-width: $md1 + px) {
      text-align: center;
      margin: 0 auto;
    }
  }

  // .info-header__text
  &__text {
    max-width: 632px;
    font-size: 22px;
    font-weight: 500;
    line-height: 1.73;
    color: #7d7d7d;
    margin: 80px 0 0;
    @media (max-width: $md1 + px) {
      text-align: center;
      margin: 60px auto;
    }
  }

  // .info-header__btn
  &__btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 22px;
    font-weight: 500;
    line-height: 1.82;
    color: #ffffff;
    width: 262px;
    height: 64px;
    border-radius: 11px;
    background-color: #717cff;
    padding: 15px 38px 14px 20px;
    margin: 40px 0 0;
    cursor: pointer;
    @media (max-width: $md1 + px) {
      margin: 40px auto 0;
    }
    img {
      height: 25px;
      width: 25px;
    }
  }

  // .info-header__row
  &__row {
    display: flex;
    align-items: center;
    margin: 40px 0 0 0;
    img {
      cursor: pointer;
      width: 25px;
      object-fit: scale-down;
    }
    @media (max-width: $md1 + px) {
      justify-content: flex-end;
      margin: 30px 0 0 0;
      img {
        width: 45px;
      }
    }
  }

  // .info-header__link
  &__link {
    margin: 0 15px 0 0;
  }
}

.header__menu {
  display: none;
}

@media (max-width: $md1 + px) {
  .header__menu {
    display: block;
    position: relative;
    min-width: 30px;
    height: 18px;
    cursor: pointer;
    margin: 0 80px 0 0;
    z-index: 3;
    &::before, &::after, span {
      position: absolute;
      left: 0;
      height: 10%;
      width: 100%;
      transition: all 0.3 ease 0s;
      background-color: #000;
    }

    &::before, &::after {
      content: "";
    } 

    &::before {
      top: 0;
    }
    &::after {
      bottom: 0;
    } 
    span {
      top: 50%;
      transform: scale(1) translate(0px, -50%);
    }
  } 
}

@media (max-width: $md4 + px) {
  .header__menu {
    margin: 0 40px 0 0;
  }}