body {
	color: #000;
	font-size: 14px;
	&._lock {
		overflow: hidden;
	}
}

._container {
	max-width: $maxWidthContainer + px;
	margin: 0 auto;

	@media (max-width: $md1+px) {
		max-width: 970px;
	}

	@media (max-width: $md2+px) {
		max-width: 750px;
	}

	@media (max-width: $md3+px) {
		max-width: none;
		padding: 0 10px;
	}
}

.page {
	flex: 1 1 auto;
}

//===================================================================================================================================

.universities {
	background-color: #fafafa;
	// .universities__content
	&__content {
		padding: 60px 0;
	}
	// .universities__title
	&__title {
		text-align: center;
		opacity: 0.73;
		font-size: 23px;
		font-weight: 500;
		letter-spacing: 2.1px;
		color: #373737;
	}
	// .universities__slider
	&__slider {
		width: 1200px;
		margin-top: 65px;
		@media (max-width: $md1+px) {
			width: 900px;
		}
		@media (max-width: $md2+px) {
			width: 700px;
		}
		@media (max-width: $md3+px) {
			width: 400px;
			margin-top: 25px;
		}
		@media (max-width: $md4+px) {
			width: 300px;
		}
	}
	// .universities__wrapper
	&__wrapper {

	}
	// .universities__slide
	&__slide {
		display: flex;
		flex-wrap: wrap;
	}
	// .universities__img
	&__img {
		margin: 25px auto 0;
		:last-child {
			margin: 25px auto 0;
		}
		@media (max-width: $md2+px) {
			padding: 0 10px 0;
		}
	}
	// .universities__points
	&__points {
		&.swiper-pagination{
			position: static;
			margin: 100px 0 0;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.swiper-pagination-bullet{
			display: block;
			background-color: #717cff;
			margin: 0 10px !important;
			width: 10px;
			height: 10px;
			&.swiper-pagination-bullet-active{
				width: 15px;
				height: 15px;
			}
		}
	}
}


// ===================================================================================================================================
 
.services {
	// .services__content
	&__content {
		padding: 124px 0;
		@media (max-width: $md3+px) {
			padding: 60px 0 80px;
		}
	}

	// .services__title
	&__title {
		text-align: center;
	}

	// .services__subtitlte
	&__subtitle {
		text-align: center;
		margin: 25px auto 0;
	}

	// .services__list
	&__list {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		margin: 60px 0 0;
		@media (max-width: $md3+px) {
			margin: 25px 0 0;
		}
	}

	// .services__cart
	&__cart {
		padding: 30px 18px 30px 30px;
		margin: 50px 25px 0;
		@media (max-width: $md4+px) {
			margin: 50px 10px 0;
		}
	}
}

.cart-services {
	max-width: 344px;
	height: 351px;
	border-radius: 15px;
	box-shadow: 0 0 77px 0 rgba(0, 0, 0, 0.06);
	@media (max-width: $md4+px) {
		height: 100%;
	}
	// .cart-services__icon
	&__icon {}

	// .cart-services__title
	&__title {
		font-size: 24px;
		font-weight: bold;
		line-height: 1.54;
		color: #37474f;
		margin: 30px 0 0;
	}

	// .cart-services__text
	&__text {
		font-size: 16px;
		font-weight: 500;
		line-height: 1.75;
		letter-spacing: 0.44px;
		color: rgba(55, 71, 79, 0.72);
		margin: 10px 0 0;
	}

	// .cart-services__link
	&__link {
		margin: 25px 0 0 0;
	}
}

// ===================================================================================================================================

.news {
	overflow: hidden;
	background-color: #fafafa;
	// .news__content
	&__content {
		padding: 110px 0;
		@media (max-width: $md3+px) {
			padding: 60px 0 80px;
		}
	}

	// .news_title
	&_title {
		text-align: center;
	}

	// .news__subtitle
	&__subtitle {
		text-align: center;
		margin: 25px auto 0;
	}

	// .news__list
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		margin: 100px -22.5px 0;
		@media (max-width: $md1+px) {
			justify-content: center;
			margin: 70px 0 0;
		}
		@media (max-width: $md3+px) {
			margin: 35px 0 0;
		}
	}

	// .news__cart
	&__cart {
		height: 406px;
		border-radius: 10px;
		box-shadow: 0 6px 42px 0 rgba(0, 0, 0, 0.1);
		margin: 50px 22.5px 0;
		@media (max-width: $md4+px) {
			margin: 50px 0 0;
			box-shadow: 0 6px 42px 0 rgba(0, 0, 0, 0);
		}
	}

	// .news__btn
	&__btn {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 226px;
		height: 64px;
		padding: 12px 32.5px 12px 34px;
		border-radius: 11px;
		background-color: #717cff;
		margin: 150px auto 0;
		img {
			width: 28.1px;
			height: 26px;
			object-fit: contain;
		}
		span {
			font-size: 22px;
			font-weight: 500;
			color: #ffffff;
		}
		@media (max-width: $md3+px) {
			margin: 25px auto 0;
		}
	}
}

.cart-news {

	// .cart-news__img
	&__img {
		max-width: 344px;
		height: 224px;
	}

	// .cart-news__data
	&__data {
		display: flex;
		margin: 25px 0 0 25px;
		img {
			margin: 0 12px 0 0;
		}
		span {
			font-size: 16px;
			font-weight: 600;
			color: #565656;
		}
	}
	// .cart-news__title
	&__title {
		margin: 25px 0 0 25px;
		width: 291px;
		font-size: 22px;
		font-weight: bold;
		color: #252525;
	}
}

//===================================================================================================================================
 
.upcoming {
	// .upcoming__content
	&__content {
		padding: 100px 0;
		@media (max-width: $md3+px) {
			padding: 60px 0;
		}
	}
	// .upcoming__title
	&__title {
		text-align: center;
	}
	// .upcoming__subtitle
	&__subtitle {
		text-align: center;
		margin: 25px auto 0;
	}
	// .upcoming__slider
	&__slider {
		width: 1200px;
		margin-top: 75px;
		@media (max-width: $md1+px) {
			width: 900px;
		}
		@media (max-width: $md2+px) {
			width: 700px;
		}
		@media (max-width: $md3+px) {
			width: auto;
		}
		@media (max-width: $md4+px) {
			width: auto;
		}
	}
	// .upcoming__wrapper
	&__wrapper {
	}
	// .upcoming__slide
	&__slide {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}
	// .upcoming__cart
	&__cart {
		width: 540px;
		border-radius: 15px;
		box-shadow: 0 0 77px 0 rgba(0, 0, 0, 0.06);
		padding: 30px;
		margin: 25px 30px;
		@media (max-width: $md3+px) {
			width: 320px;
			border-radius: 0;
			box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
			margin: 25px 30px 0;
		}
	}
	// .upcoming__points
	&__points {
		&.swiper-pagination{
			position: static;
			margin: 80px 0 0;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.swiper-pagination-bullet{
			display: block;
			background-color: #717cff;
			margin: 0 10px !important;
			width: 10px;
			height: 10px;
			&.swiper-pagination-bullet-active{
				width: 15px;
				height: 15px;
			}
		}
	}
}

.cart-upcoming {
	// .cart-upcoming__top
	&__top {
		display: flex;
		align-items: center;
	}
	// .cart-upcoming__data
	&__data {
		min-width: 76px;
		height: 76px;
		border-radius: 11px;
		background-color: rgba(113, 124, 255, 0.14);
		padding: 10px 0 0;
		margin: 0 20px 0 0;
		text-align: center;
		span{
			opacity: 1;
			display: block;
			font-weight: bold;
			color: #717cff;
		}
		span:first-child{
			font-size: 27px;
		}
		span:last-child{
			font-size: 18px;
			margin: 5px 0 0;
		}
	}
	// .cart-upcoming__title
	&__title{
		width: 352px;
		font-size: 24px;
		font-weight: bold;
		line-height: 1.54;
		color: #37474f
	}
	// .cart-upcoming__text
	&__text {
		font-size: 16px;
		font-weight: 500;
		line-height: 1.75;
		letter-spacing: 0.44px;
		color: rgba(55, 71, 79, 0.72);
		margin: 25px 0 0;
	}
	// .cart-upcoming__bottom
	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 25px 0 0;
		@media (max-width: $md3+px) {
			display: block;
		}
	}
	// .cart-upcoming__btn
	&__btn {

	}
	// .cart-upcoming__time
	&__time {
		display: flex;
		align-items: center;
		span{
			font-size: 16px;
			font-weight: 600;
			line-height: 1.75;
			letter-spacing: 0.44px;
			color: #373737;
			margin: 0 0 0 5px;
		}
		@media (max-width: $md3+px) {
			margin: 10px 0 0;
			justify-content: flex-end;
		}
	}
}


