//===================================================================================================================================
 
.ready {
	background-color: #fafafa;
	// .ready__content
	&__content {
		padding: 75px 0;
		@media (max-width: $md3+px) {
			padding: 50px 0;
		}
	}
	// .ready__row
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		@media (max-width: $md2+px) {
			justify-content: center;
			text-align: center;
		}
	}
	// .ready__text
	&__text {
		display: block;
		width: 538px;
		font-size: 37px;
		font-weight: bold;
		color: #252525;
		@media (max-width: $md3+px) {
			font-size: 30px;
		}
	}
	// .ready__btn
	&__btn {
		display: flex;
		align-self: center;
		min-width: 262px;
		height: 64px;
		padding: 21px 32.5px 0;
		border-radius: 11px;
		background-color: #717cff;
		img {
			height: 25px;
			width: 25px;
		}
		span {
			display: block;
			width: 138px;
			font-size: 22px;
			font-weight: 500;
			color: #ffffff;
			margin: 0 auto 0 0;
		}
		@media (max-width: $md2+px) {
			margin: 35px 0 0;
		}
	}
}